/*
 * Styles for the Glitch editor page
 * These will not be included in the downloadable plugin
 */

@import "common.scss";

html,
body,
#editor {
  height: 100%;
}

body {
  padding: 0;
  font-family: -apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,Oxygen-Sans,Ubuntu,Cantarell,"Helvetica Neue",sans-serif;
}

section {
  border-bottom: 1px solid $gray;
  padding: 1em 2em;
}

@media only screen and ( min-width: 1280px ) {
  
  .playground {
    overflow: hidden;
    border-bottom: 1px solid $gray;
    height: 80%;
  }
  
  .playground section {
    float: left;
    width: 50%;
    border-bottom: none;
    height: 100%;
    overflow: auto;
    
    &:last-child {
      border-left: 1px solid $gray;
    }
  }
}